//colors
$color_black: black;
$color_white_50: rgba(255, 255, 255, 0.5);
$color_white_15: rgba(255, 255, 255, 0.15);
$color_white: #fff;

.devnet_fsl-free-shipping {
	width: 100%;
	margin: 1rem 0 2rem;
	padding: 1rem 2rem;
	box-shadow: 0 0 2rem -1rem $color_black;
	box-sizing: border-box;

	&:after {
		content: "";
	}

	&.fsl-flat {
		margin: 0;
		padding: 0;
	}

	* {
		box-sizing: border-box;
	}


	.fsl-reached-message {
		display: block;
		position: relative;
		padding-bottom: 0.5rem;
		line-height: 1.2;
		border-bottom: 1px solid var(--fsl-bar-border-color);

		font: {
			weight: bold;
			size: 1rem;
		}

		&:before {
			content: '\2713';
			margin-right: 0.5rem;
			width: 2rem;
			font-weight: bold;
			color: var(--fsl-bar-inner-color, green);
		}

		&:not(:first-child) {
			margin-top: 1rem
		}

		&+.fsl-title {
			margin-top: 1rem;
		}
	}

	&.qualified-message {

		&.fsl-has-reached {
			// display: block;
			//width: auto;
			//width: fit-content;
			//flex: 1 0 100%;
		}

		.fsl-reached-message {
			border-bottom: none;
			margin: 0;

			&:last-child {
				padding-bottom: 0;
			}
		}
	}

	.fsl-title,
	.title {
		width: 100%;
		display: block;
		margin: 0 auto;
		font-size: 1.1em;

		.fsl-gift-bar-title {
			display: block;
			margin-top: 1rem;
		}
	}

	.fsl-description,
	.fsl-notice,
	.notice {
		width: 100%;
		display: block;

		.woocommerce-Price-amount.amount {
			font-weight: bold;
		}
	}


	.fsl-progress-bar,
	.progress-bar {
		width: 100%;
		height: auto;
		justify-content: flex-start;
		margin: 1rem 0;
		border: 0.0625rem solid $color_black;
		border-radius: var(--fsl-bar-border-radius, '8px');
		box-shadow: 0 0.3rem 1rem -0.5rem $color_black;
		//reset third-party styles
		float: none !important;
		display: block;
		visibility: visible;
		padding: 0;

		.fsl-progress-amount,
		.progress-amount {
			position: relative;
			display: block;
			border-radius: var(--fsl-bar-border-radius, '8px');
		}

		span {
			display: inline-block;
			height: 100%;
			border-radius: 3px;
			//box-shadow: 0 1px 0 $color_white_50 inset;
			transition: width 0.4s ease-in-out;
		}

		&.shine {
			span {
				position: relative;
			}

			span.fsl-progress-amount::after {
				content: '';
				opacity: 0;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background: $color_white;
				border-radius: 3px;
				animation: fsl-animate-shine 2s ease-out infinite;
			}
		}

		&.stripes span.fsl-progress-amount {
			background-size: 30px 30px;
			background-image: linear-gradient(135deg,
					$color_white_15 25%,
					transparent 25%,
					transparent 50%,
					$color_white_15 50%,
					$color_white_15 75%,
					transparent 75%,
					transparent);
			animation: fsl-animate-stripes 3s linear infinite;

			&:dir(rtl) {
				animation: fsl-animate-stripes 3s linear infinite reverse;
			}
		}
	}

	&.qualified-message {
		.title {
			margin: 0;
			padding: 0;
		}
	}

	&.fsl-center-text {
		text-align: center;

		.fsl-reached-message,
		.fsl-title,
		.fsl-notice,
		.fsl-description {
			text-align: center;
		}
	}

	&.fsl-no-stripes {
		.stripes span {
			background-size: 30px 30px;
			background-image: none;
			animation: none;
		}
	}
}

@keyframes fsl-animate-stripes {
	0% {
		background-position: 0 0;
	}

	100% {
		background-position: 60px 0;
	}
}

@keyframes fsl-animate-shine {
	0% {
		opacity: 0;
		width: 0;
	}

	50% {
		opacity: 0.5;
	}

	100% {
		opacity: 0;
		width: 100%;
	}
}

.devnet_fsl-no-shadow {
	border: none;
	box-shadow: none;
}

.devnet_fsl-label {
	display: block !important;
	margin: 1rem auto;
	padding: 0.3rem 0.5rem;
	font-size: 0.8em;
	font-weight: bold;
	text-align: center;
	box-shadow: 0 5px 16px -8px $color_black;
}

