.devnet_fsl-no-animation.devnet_fsl-disabled-animation {

	&-shine {
		.fsl-progress-amount:after {
			animation: none !important;
		}
	}

	&-strips {
		.fsl-progress-amount {
			animation: none !important;
		}
	}

	&-all {

		.fsl-progress-amount,
		.fsl-progress-amount:after {
			animation: none !important;
		}
	}
}

.summary .devnet_fsl-label {
	max-width: max-content;
	margin: inherit;
	margin: 0.5rem 0 1rem;
}

