// PROGRESS BAR

.devnet_fsl-free-shipping {

	.title,
	.notice {
		color: var(--fsl-text-color);
	}

	.fsl-progress-bar.progress-bar {
		flex-basis: 100%;
		position: relative;

		&.fsl-bar-width-adjust {
			width: calc(100% - var(--fsl-bar-width-adjust, 0));
			flex-basis: calc(100% - var(--fsl-bar-width-adjust, 0));
		}

		.fsl-threshold-indicator {
			--fsl-icon-container-size: calc(var(--fsl-icon-size, 0px) * 1.5);

			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: calc(var(--fsl-module-threshold) - var(--fsl-icon-container-size) / 2);
			height: var(--fsl-icon-container-size);
			display: flex;
			justify-content: center;
			align-items: center;
			z-index: 0;

			&:dir(rtl) {
				left: unset;
				right: calc(var(--fsl-module-threshold) - var(--fsl-icon-container-size) / 2);
			}

			&.fsl-has-threshold-icon {
				--fsl-icon-container-size: calc(var(--fsl-icon-size, var(--fsl-bar-height)) * 1.5);
				--fsl-icon-container-border-width: 1px;

				background: var(--fsl-icon-bg-color, #ffffff);
				border: var(--fsl-icon-container-border-width) solid #333333;
				border-radius: 100%;
				width: var(--fsl-icon-container-size);


				&.fsl-icon-shape-round {
					border-radius: 100%;
				}

				&.fsl-icon-shape-rounded {
					border-radius: 20%;
				}

				&.fsl-icon-shape-square {
					border-radius: 0;
				}


				.fsl-svg-icon>*,
				.fsl-svg-icon g path {
					fill: var(--fsl-icon-color);
				}

				.fsl-svg-icon>rect {
					fill: transparent;
				}


				&:after {
					content: none;
				}

				&.fsl-threshold-reached {
					box-shadow: inset 0 0 6px 0 var(--fsl-bar-inner-color, #8bc34a);
					border-color: var(--fsl-bar-inner-color, #8bc34a);
				}
			}

			&.fsl-has-threshold-bubble {

				// background: none;
				// box-shadow: none;

				// svg {
				// 	display: none;
				// }
				z-index: var(--fsl-indicator-z-index);

				&:hover {
					z-index: 2;
				}

				.fsl-threshold-bubble {
					--fsl-pointer-width: 5px;
					--fsl-pointer-height: 16px;

					position: absolute !important;
					right: -1rem;
					bottom: calc((var(--fsl-bar-height) / 2) + var(--fsl-pointer-height));
					padding: 0.5rem;
					border-radius: 8px;
					width: 10rem;
					height: auto;
					background: white;
					box-shadow: 0 0 9px -3px black;
					cursor: default;
					font-size: 0.8em;

					&:dir(rtl) {
						right: unset;
						left: -1rem;
					}

					// Reset inner spans
					&>span {
						height: auto;
					}

					&:after {
						content: '';
						position: absolute;
						top: 100%;
						right: calc(1rem - var(--fsl-pointer-width));
						width: 0;
						height: 0;
						border-style: solid;
						border-right: var(--fsl-pointer-width) solid transparent;
						border-left: var(--fsl-pointer-width) solid transparent;
						border-top: var(--fsl-pointer-height) solid var(--fsl-bar-inner-color, #ffffff);
						border-bottom: 0;
					}

					&:dir(rtl) {
						&:after {
							right: unset;
							left: calc(1rem - var(--fsl-pointer-width));
						}
					}
				}

				&:nth-child(2) .fsl-threshold-bubble {}

				&:nth-child(3) .fsl-threshold-bubble {}

				&:nth-child(4) .fsl-threshold-bubble {}

				&.fsl-has-threshold-icon {

					.fsl-threshold-bubble {
						bottom: calc(var(--fsl-bar-height) + (var(--fsl-icon-container-size) / 2) + var(--fsl-pointer-height));

						&:after {
							// Calculation: bubble offset, pointer, half icon container, border and half
							right: calc(1rem - var(--fsl-pointer-width) + (var(--fsl-icon-container-size) / 2) - (var(--fsl-icon-container-border-width) * 1.5));
						}

						&:dir(rtl) {
							&:after {
								right: unset;
								left: calc(1rem - var(--fsl-pointer-width) + (var(--fsl-icon-container-size) / 2) - (var(--fsl-icon-container-border-width) * 1.5));
							}
						}

					}
				}

			}

		}
	}

	&.fsl-layout-threshold_bubbles {
		//padding-top: 6rem;
		box-shadow: unset;

		// Copy the bubble styles
		.fsl-height-adjuster {
			font-size: 0.8em;
			width: 10rem;
			padding: 0.5rem;
			margin-bottom: 16px;
			color: transparent;
			visibility: hidden;
			opacity: 0;
			pointer-events: none;
		}
	}


	&.fsl-layout-horizontal_1,
	&.fsl-layout-horizontal_2 {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;


		.fsl-module-block {
			display: inline-block;
			max-width: calc(100% / 2 - 0.5rem);

			.fsl-title {
				text-align: left;
			}
		}
	}

	&.fsl-layout-list,
	&.fsl-layout-list_alt {

		ul.fsl-modules-list {
			list-style: none;
			margin: 0;
			padding: 0;
			padding-left: 1rem;
			//border-left: 2px solid;
			position: relative;

			&::before {
				content: '';
				position: absolute;
				top: 2px;
				bottom: 6px;
				left: -2px;
				width: 2px;
				background-color: #000;
			}

			li {
				position: relative;
				margin: 0;
				padding: 0;
				margin-bottom: 1rem;

				&:before {
					content: "";
					position: absolute;
					top: 2px;
					left: -1.55rem;
					width: 1rem;
					height: 1rem;
					border-radius: 100%;
					background-color: #ffffff;
					border: 0.25rem solid var(--fsl-bar-border-color, '#000000');
					box-sizing: border-box;
				}

				&:last-child {
					margin-bottom: 0;
				}

				&.fsl-reached {
					margin-left: 0.5rem;

					&:before {
						content: '\2713';
						display: flex;
						justify-content: center;
						align-items: center;
						color: var(--fsl-bar-inner-color, 'green');
						border-width: 0.2rem;
						width: 1.5rem;
						height: 1.5rem;
						top: 0;
						left: -2.3rem;
						font-size: 1rem;
						font-weight: bold;
					}

				}

				.fsl-title {
					text-align: left;
					line-height: 1.3;
					margin: 0;
				}

				.fsl-description {
					text-align: left;
				}

			}
		}

		&:dir(rtl) {
			ul.fsl-modules-list {
				padding-left: unset;
				padding-right: 1rem;
				border-left: unset;
				border-right: 2px solid;

				li {

					&:before {
						left: unset;
						right: -1.55rem;
					}

					&.fsl-reached {
						margin-left: unset;
						margin-right: 0.5rem;

						&:before {
							left: unset;
							right: -2.3rem;
						}
					}

					.fsl-title {
						text-align: right;
					}

					.fsl-description {
						text-align: right;
					}

				}
			}
		}
	}

	&.fsl-layout-stacked {
		display: flex;
		flex-direction: column;

		.fsl-module-block {
			position: relative;
			margin-bottom: 0.5rem;
			padding-bottom: 0.5rem;
			border-bottom: 0.1rem solid;

			.fsl-title,
			.fsl-description {
				text-align: left;
			}

			&:first-child {}

			&:last-child {}
		}
	}


	&.fsl-layout-timeline {
		display: flex;
		flex-direction: column;

		.fsl-timeline-block {
			position: relative;
			padding: 0.3rem 1rem;
			box-shadow: 0 0 6px -2px black;
			background-color: #fff;
			border-radius: 0.5rem;
			box-shadow: 0 0 6px -2px black;

			&:before {
				content: '';
				position: absolute;
				width: 0;
				height: 0;
				left: var(--fsl-timeline-threshold);
				border: .75rem solid transparent;
			}

			.fsl-title,
			.fsl-description {
				text-align: left;
			}

			&:first-child {
				order: 1;

				&:before {
					top: 100%;
					border-bottom: none;
					border-top-color: #fff;
					filter: drop-shadow(0 2px 1px #c7c7c7);
				}
			}

			&:nth-child(2) {
				order: 10;
				border-top-right-radius: 0;

				&:before {
					top: -0.75rem;
					right: 0;
					left: unset;
					border-top: none;
					border-bottom-color: #fff;
					filter: drop-shadow(0 -2px 1px #c7c7c7);
				}
			}
		}

		.fsl-progress-bar {
			order: 5;
		}

	}
}

.fsl-circular-bar-wrapper {
	width: var(--fsl-circle-size);
	height: var(--fsl-circle-size);
	margin: 1rem auto;
	position: relative;

	&:nth-child(3n + 1) {
		clear: both;
	}

	.fsl-circular-bar {
		width: 100%;
		height: 100%;
		clip: rect(0,
				var(--fsl-circle-size),
				var(--fsl-circle-size),
				var(--fsl-circle-size-half));
		left: 0;
		position: absolute;
		top: 0;

		.fsl-half-circle {
			width: 100%;
			height: 100%;
			color: var(--fsl-circle-bar-inner-color, inherit);
			border-color: currentColor;
			border: var(--fsl-circle-size-tenth) solid currentColor;
			border-radius: 50%;
			clip: rect(0, var(--fsl-circle-size-half), var(--fsl-circle-size), 0);
			left: 0;
			position: absolute;
			top: 0;
		}

		.fsl-left-side {
			transform: rotate(var(--fsl-circle-left-rotation));
		}

		&.less-than-50 {
			.fsl-right-side {
				display: none;
			}
		}

		&.more-than-50 {
			clip: rect(auto, auto, auto, auto);

			.fsl-right-side {
				transform: rotate(180deg);
			}
		}
	}

	.fsl-inner-circle {
		background-color: var(--fsl-circle-bg-color, transparent);
		position: absolute;
		top: var(--fsl-circle-size-tenth);
		left: var(--fsl-circle-size-tenth);
		right: var(--fsl-circle-size-tenth);
		bottom: var(--fsl-circle-size-tenth);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		color: var(--fsl-text-color, currentColor);
		text-align: center;
		padding: var(--fsl-circle-size-tenth);
		cursor: default;

		.fsl-svg-icon>*,
		.fsl-svg-icon g path {
			fill: var(--fsl-circular-bar-icon-color);
		}

		.fsl-svg-icon>rect {
			fill: transparent;
		}
	}

	.fsl-circular-bar-background {
		width: 100%;
		height: 100%;
		border: var(--fsl-circle-size-tenth) solid var(--fsl-circle-bar-bg-color, transparent);
		border-radius: 50%;
	}

	&.animation {
		&.puls {
			.fsl-half-circle {
				animation: fsl-animate-invert-puls 2s infinite;
			}

			.fsl-inner-circle {
				//animation: fsl-animate-opacity-puls 2s infinite;
			}
		}
	}
}

