@keyframes fsl-animate-invert-puls {
	0% {
		filter: invert(0);
	}

	50% {
		filter: invert(35%);
	}

	100% {
		filter: invert(0);
	}
}

@keyframes fsl-animate-opacity-puls {
	0% {
		opacity: 1;
	}

	50% {
		opacity: 0.5;
	}

	100% {
		opacity: 1;
	}
}

// NOTICE BAR
.fsl-wrapper.notice-bar,
.devnet_fsl-free-shipping.notice-bar {
	position: fixed;
	max-width: 320px;
	margin: 0;
	background-color: $color_white;
	z-index: 9999;

	.fsl-close-notice-bar {
		position: absolute;
		top: -12px;
		right: -12px;
		border-radius: 100%;
		background-color: lightgray;
		width: 1rem;
		height: 1rem;
		padding: 6px;
		display: flex;
		align-items: center;
		justify-content: center;
		box-sizing: content-box;
		cursor: pointer;

		&:hover {
			background-color: darkgrey;
		}
	}

	&.fsl-multiple-notice-bars {
		display: flex;
		flex-direction: column;

		.notice-bar {
			position: sticky;
			margin-bottom: 1rem;
		}
	}

	&.autohide {
		&.top-left {
			top: var(--fsl-notice-bar-margin-y, 32px);
			left: -100vw;
			animation: noticeBarFadeInOutLeft 5s;
		}

		&.top-right {
			top: var(--fsl-notice-bar-margin-y, 32px);
			right: -100vw;
			animation: noticeBarFadeInOutRight 5s;
		}

		&.bottom-left {
			bottom: var(--fsl-notice-bar-margin-y, 32px);
			left: -100vw;
			animation: noticeBarFadeInOutLeft 5s;
		}

		&.bottom-right {
			bottom: var(--fsl-notice-bar-margin-y, 32px);
			right: -100vw;
			animation: noticeBarFadeInOutRight 5s;
		}
	}

	&.hold {
		&.top-left {
			top: var(--fsl-notice-bar-margin-y, 32px);
			left: var(--fsl-notice-bar-margin-x, 32px);
			animation: noticeBarFadeInLeft 0.5s;
		}

		&.top-right {
			top: var(--fsl-notice-bar-margin-y, 32px);
			right: var(--fsl-notice-bar-margin-x, 32px);
			animation: noticeBarFadeInRight 0.5s;
		}

		&.bottom-left {
			bottom: var(--fsl-notice-bar-margin-y, 32px);
			left: var(--fsl-notice-bar-margin-x, 32px);
			animation: noticeBarFadeInLeft 0.5s;
		}

		&.bottom-right {
			bottom: var(--fsl-notice-bar-margin-y, 32px);
			right: var(--fsl-notice-bar-margin-x, 32px);
			animation: noticeBarFadeInRight 0.5s;
		}
	}
}

@keyframes noticeBarFadeInLeft {
	0% {
		left: -100vw;
		opacity: 0;
	}

	100% {
		left: var(--fsl-notice-bar-margin-x, 32px);
		opacity: 1;
	}
}

@keyframes noticeBarFadeInRight {
	0% {
		right: -100vw;
		opacity: 0;
	}

	100% {
		right: var(--fsl-notice-bar-margin-x, 32px);
		opacity: 1;
	}
}

@keyframes noticeBarFadeInOutLeft {
	0% {
		left: -100vw;
		opacity: 0;
	}

	5% {
		left: var(--fsl-notice-bar-margin-x, 32px);
		opacity: 1;
	}

	90% {
		left: var(--fsl-notice-bar-margin-x, 32px);
		opacity: 1;
	}

	100% {
		left: -100vw;
		opacity: 0;
	}
}

@keyframes noticeBarFadeInOutRight {
	0% {
		right: -100vw;
		opacity: 0;
	}

	5% {
		right: var(--fsl-notice-bar-margin-x, 32px);
		opacity: 1;
	}

	90% {
		right: var(--fsl-notice-bar-margin-x, 32px);
		opacity: 1;
	}

	100% {
		right: -100vw;
		opacity: 0;
	}
}

// PRODUCT LABEL

.devnet_fsl-label-image {
	.fsl-label-image {
		width: var(--fsl-label-image-width, 100px) !important;
		height: auto !important;
		object-fit: contain !important;
	}
}

.devnet_fsl-label-over-image {
	display: block;
	position: absolute;
	z-index: 5;

	&.top-left {
		top: var(--fsl-label-margin-y, 0px);
		left: var(--fsl-label-margin-x, 0px);
	}

	&.top-right {
		top: var(--fsl-label-margin-y, 0px);
		right: var(--fsl-label-margin-x, 0px);
	}

	&.bottom-left {
		bottom: var(--fsl-label-margin-y, 0px);
		left: var(--fsl-label-margin-x, 0px);
	}

	&.bottom-right {
		bottom: var(--fsl-label-margin-y, 0px);
		right: var(--fsl-label-margin-x, 0px);
	}

	.devnet_fsl-label {
		margin: 0;
	}
}


